@font-face {
    font-family: 'Metropolis';
    src: url('../fonts/Metropolis-Regular.otf');
    font-weight: 400;
}

@font-face {
    font-family: 'Metropolis';
    src: url('../fonts/Metropolis-Bold.otf');
    font-weight: 700;
}

@font-face {
    font-family: 'Metropolis';
    src: url('../fonts/Metropolis-ExtraBold.otf');
    font-weight: 800;
}

/* --------------- */

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    font-family: 'Metropolis';
}

h1, h2, h3 {
    line-height: 62px;
    color: #232427;
}

h1 {
    font-size: 48px;
    line-height: 1.291em;
}

h2 {
    font-size: 36px;
    line-height: 1.22em;
}

h3 {
    font-weight: 800;
    font-size: 18px;
    line-height: 1em;
}

p {
    font-size: 14px;
    line-height: 1.7em;
    color: #9998A4;
}

a {
    font-size: 14px;
    font-weight: 700;
    color: #5E5C6C;
    text-decoration: none;
    text-transform: capitalize;
}

img {
    width: 100%;
    object-fit: cover;
}

button {
    padding: 15px 20px;
    background: #2962EF;
    border-radius: 4px;
    border: none;
    cursor: pointer;

    font-size: 14px;
    color: #FFFFFF;

    transition: .2s;
}   

button:hover {
    color: #2962EF;
    background: #fff;
}

button.reverse {
    color: #2962EF;
    background: #fff;
}

button.reverse:hover {
    color: #fff;
    background: #2962EF;
}

input {
    font-size: 14px;
    color: #232427;

    border: none;
    outline: none;
}

/* classes ------------------------ */

.container {
    width: 100%;
    max-width: 1110px;
    padding: 0px 15px;
    margin: 0 auto;
}

.flex, nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* site ----------------------- */

/* components ------------ */

.list {
    width: 100%;
    flex-wrap: wrap;
}

.list > div {
    margin: 0px 5px;
}

/* header ------------- */
.header {
    width: 100%;
    padding-top: 15px;
}

.header nav {
    max-width: 440px;
    width: 100%;
}


.header img {
    width: 18px;
    height: 18px;
    margin-right: 5px;
    vertical-align: middle;
    object-fit: contain;
}

/* intro ---------------------- */

.intro {
    width: 100%;
    margin-top: 100px;
}

.intro > img {
    width: 100%;
    max-width: 900px;
    position: absolute;
    right: 0;
    top: 70px;

    z-index: -1;
}

.intro__container {
    max-width: 530px;
    width: 100%;
}

.intro__container > p {
    margin-top: 15px;
}

.intro__btns {
    max-width: 300px;
    width: 100%;
    margin-top: 60px;
}

.intro__btns button {
    margin: 0px 5px;
}

.intro__btns button:first-child {
    margin-left: 0;
}

.intro__footer {
    width: 100%;
    margin-top: 130px;
}

.intro__footer > p {
    text-transform: uppercase;
}

.intro__footer-container {
    width: 100%;
    margin-top: 25px;
}

.intro__footer-item {
    margin: 0px 5px;
}

.intro__footer-item h2 {
    font-weight: 400;
}

.intro__footer-item p {
    color: #5E5C6C;
    text-transform: uppercase;
}

/* steps ----------------- */

.steps {
    width: 100%;
    margin-top: 110px;
}

.steps__item {
    max-width: 345px;
    width: 100%;
    margin-top: 50px;
    padding: 60px 40px;

    border: 1px solid #EFEFF4;
    border-radius: 8px;
}

.steps__item img {
    width: 95px;
    height: 95px;
    padding: 30px;

    background: #FAFBFD;
    border-radius: 50%;
}

.steps__item h3 {
    margin-top: 40px;
}

.steps__item p {
    margin-top: 15px;
}

.steps__item h2 {
    margin-top: 15px;
}

/* company ------------------- */

.company {
    width: 100%;
    margin-top: 110px;
}

.company h2 {
    font-size: 28px;
    text-align: center;
}

.company__container {
    flex-wrap: wrap;
    justify-content: space-around;
}

.company__container img {
    margin: 40px 10px 0;
    width: auto;
}

/* persons ---------------- */

.persons {
    width: 100%;
    margin-top: 140px;
}

.persons > p {
    margin-top: 15px;
}

.persons__container {
    width: 100%;
    margin-top: 45px;
}

.persons__item {
    max-width: 255px;
    width: 100%;
    padding: 20px;
    margin: 0px 10px;

    background: #FAFBFD;
    border-radius: 4px;

    transition: .2s;
}

.persons__item:hover {
    background: #FFFFFF;
    box-shadow: 0px 4px 144px rgba(0, 0, 0, 0.2);
}

.persons__item h3 {
    font-size: 16px;
    margin: 15px 0px 10px;
}

.persons__item-message {
    max-width: 40px;
}

.persons__item-message img {
    width: 15px;
    height: 15px;
    margin: 0px 10px;
}

.persons__item-message img:first-child {
    margin-left: 0;
}

/* footer ----------------------- */

.footer {
    width: 100%;
    margin-top: 110px;
    padding: 90px 180px;

    background: #232427;
}

.footer__container {
    width: 100%; 
}

.footer__item {
    max-width: 345px;
    width: 100%;
}

.footer__item h3,
.footer__item > p {
    color: #fff;
}

.footer__item h3 {
    margin-bottom: 20px;
}

.footer__item:first-child {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    height: 100%;
}

.footer__item:first-child div p {
    color: #D6D5DF;
}

/* ------ */

.footer__item-title {
    font-size: 16px;
    font-weight: 600;
}

.footer__message img {
    width: 50px;
    height: 50px;
    padding: 15px;
    margin-top: 35px;

    object-fit: contain;
    background: #222125;
    border-radius: 50%;
}

.footer__send {
    max-width: 345px;
    width: 100%;
    margin: 10px 0;

    position: relative;
}

.footer__send input {
    width: 100%;
    padding: 20px;
}

.footer__send input:placeholder {
    color: #9998A4;
}

.footer__send button {

    padding: 10px;

    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
}

@media (max-width: 1000px) {
    /* intro --------- */
    .intro > img {
        max-width: 600px;
        top: 25%;
    }

    /* steps --------------- */
    .steps__item {
        max-width: 380px;
    }

    /* persons --------- */
    .persons__item {
        max-width: 200px;
    }

    /* footer -------------- */
    .footer {
        padding: 90px 10px;
    }
}

@media (max-width: 870px) {
    /* steps -------------- */
    .steps__item {
        max-width: 340px;
    }

    /* persons ------------- */

    .persons__container {
        justify-content: space-around;
    }

    .persons__item {
        max-width: 280px;
    }
}

@media (max-width: 730px) {
    /* intro --------------- */
    .intro__container {
        max-width: 380px;
    }

    .intro > img {
        max-width: 420px;
        top: 35%;
    }

    /* steps -------------- */

    .steps {
        justify-content: center;
    }

    .steps__item {
        max-width: 80%;
        text-align: center;
    }
}

@media (max-width: 550px) {

    h1 {
        font-size: 38px;
    }

    h2 {
        font-size: 28px;
    }

    h3 {
        font-size: 16px;
    }

    p {
        font-size: 12px;
    }

    /* header -------------- */

    .header nav {
        width: auto;
    }

    .header nav a {
        display: none;
    }

    /* footer --------------- */

    .footer__container {
        flex-direction: column;
    }

    .footer__item:last-child {
        margin-top: 30px;
    }

}

@media (max-width: 400px) {
    .steps__item {
        padding: 60px 0px;
    }
}